<template>
  <right-page :header="rightPageHeader" :hide-content-padding="true">
    <div slot="content">
      <section class="slds-p-around_large">
        <h3 class="slds-section__title slds-theme_shade">
            <span class="slds-p-horizontal--small slds-truncate">
                Mainland China Settings
            </span>
        </h3>
        <div class="slds-grid slds-p-around_medium slds-p-bottom_large">
          <div class="slds-form-element slds-size_1-of-2 slds-p-right_large">
            <label class="slds-form-element__label" for="consumerKey">
              <abbr class="slds-required" title="required">* </abbr>SMS Template
            </label>
            <div class="slds-form-element__control">
              <lookup
                :ld-selected-item="mainlandSmsTemplate"
                :ld-metadatas="smsTemplateMetadata"
                @select="onMainlandSmsTemplateSelect($event)"
                @remove="onMainlandSmsTemplateRemove()"
              />
            </div>
          </div>
          <div class="slds-form-element slds-size_1-of-2 slds-p-left_large">
            <label class="slds-form-element__label" for="consumerSecret">
              <abbr class="slds-required" title="required">* </abbr>
              SMS Signature
            </label>
            <div class="slds-form-element__control">
              <lookup
                :ld-selected-item="mainlandSignature"
                :ld-metadatas="signatureMetadata"
                @select="onMainlandSmsSignatureSelect($event)"
                @remove="onMainlandSmsSignatureRemove()"
              />
            </div>
          </div>
        </div>
        <h3 class="slds-section__title slds-theme_shade">
            <span class="slds-p-horizontal--small slds-truncate">
                International / Hong Kong, Macao and Taiwan Settings
            </span>
        </h3>
        <div class="slds-grid slds-p-around_medium">
          <div class="slds-form-element slds-size_1-of-2 slds-p-right_large">
            <span class="slds-form-element__label">SMS Template</span>
            <div class="slds-form-element__control">
              <lookup
                :ld-selected-item="internationalTemplate"
                :ld-metadatas="smsTemplateMetadata"
                @select="onInternationalSmsTemplateSelect($event)"
                @remove="onInternationalSmsTemplateRemove()"
              />
            </div>
          </div>
          <div class="slds-form-element slds-size_1-of-2 slds-p-left_large">
            <span class="slds-form-element__label">SMS Signature</span>
            <div class="slds-form-element__control">
              <lookup
                :ld-selected-item="internationalSignature"
                :ld-metadatas="signatureMetadata"
                @select="onInternationalSmsSignatureSelect($event)"
                @remove="onInternationalSmsSignatureRemove()"
              />
            </div>
          </div>
        </div>
      </section>
      <footer class="slds-modal__footer">
        <button class="slds-button slds-button_neutral" :disabled="!hasChanged || isSaving" @click="onClickCancel">Cancel</button>
        <button class="slds-button slds-button_brand" :disabled="!hasChanged || isSaving || !canSave" @click="onClickSave">Save</button>
      </footer>
    </div>
  </right-page>
</template>
<script>
import Lookup from '@/components/Lookup/Lookup.vue'
import { CharketSmsSettingsService } from '../service'

export default {
  components: {
    Lookup
  },
  data () {
    return {
      rightPageHeader: {
        label: 'CHARKET SMS',
        name: 'Verification Code Settings'
      },
      isSaving: false,
      selected: {},
      smsTemplateMetadata: {
        name: 'CharketApp__SmsTemplate__c',
        label: 'SMS Template',
        labelPural: 'SMS Template',
        icon: 'SMS_template',
        linkField: {
          name: 'Name',
          label: 'Name'
        },
        fields: []
      },
      signatureMetadata: {
        name: 'CharketApp__SmsSignature__c',
        label: 'SMS Signature',
        labelPural: 'SMS Signature',
        icon: 'SMS_signature',
        linkField: {
          name: 'Name',
          label: 'Name'
        },
        fields: []
      },
      settings: {
        MainlandSettings: {
          SmsTemplate: {
            Id: null,
            Name: null
          },
          SmsSignature: {
            Id: null,
            Name: null
          }
        },
        InternationalSettings: {
          SmsTemplate: {
            Id: null,
            Name: null
          },
          SmsSignature: {
            Id: null,
            Name: null
          }
        }
      },
      sourceSettings: {
        MainlandSettings: {
          SmsTemplate: {
            Id: null,
            Name: null
          },
          SmsSignature: {
            Id: null,
            Name: null
          }
        },
        InternationalSettings: {
          SmsTemplate: {
            Id: null,
            Name: null
          },
          SmsSignature: {
            Id: null,
            Name: null
          }
        }
      }
    }
  },
  computed: {
    canSave () {
      return this.settings.MainlandSettings.SmsTemplate.Id && this.settings.MainlandSettings.SmsSignature.Id
    },
    mainlandSmsTemplate () {
      return this.settings.MainlandSettings.SmsTemplate.Id ? this.settings.MainlandSettings.SmsTemplate : null
    },
    mainlandSignature () {
      return this.settings.MainlandSettings.SmsSignature.Id ? this.settings.MainlandSettings.SmsSignature : null
    },
    internationalTemplate () {
      return this.settings.InternationalSettings.SmsTemplate.Id ? this.settings.InternationalSettings.SmsTemplate : null
    },
    internationalSignature () {
      return this.settings.InternationalSettings.SmsSignature.Id ? this.settings.InternationalSettings.SmsSignature : null
    },
    hasChanged () {
      let hasChanged = false
      Object.keys(this.settings).forEach(key => {
        const sourceTemplateId = this.sourceSettings[key].SmsTemplate ? this.sourceSettings[key].SmsTemplate.Id : null
        const templateId = this.settings[key].SmsTemplate ? this.settings[key].SmsTemplate.Id : null
        const sourceSignatureId = this.sourceSettings[key].SmsSignature ? this.sourceSettings[key].SmsSignature.Id : null
        const signatureId = this.settings[key].SmsSignature ? this.settings[key].SmsSignature.Id : null
        if (sourceTemplateId !== templateId || sourceSignatureId !== signatureId) {
          hasChanged = true
        }
      })
      return hasChanged
    }
  },
  created () {
    this.getSettings()
  },
  methods: {
    onClickCancel () {
      this.settings = JSON.parse(JSON.stringify(this.sourceSettings))
    },
    onMainlandSmsTemplateSelect (data) {
      this.settings.MainlandSettings.SmsTemplate = {
        Id: data.id,
        Name: data.name
      }
    },
    onMainlandSmsSignatureSelect (data) {
      this.settings.MainlandSettings.SmsSignature = {
        Id: data.id,
        Name: data.name
      }
    },
    onInternationalSmsTemplateSelect (data) {
      this.settings.InternationalSettings.SmsTemplate = {
        Id: data.id,
        Name: data.name
      }
    },
    onInternationalSmsSignatureSelect (data) {
      this.settings.InternationalSettings.SmsSignature = {
        Id: data.id,
        Name: data.name
      }
    },
    onMainlandSmsTemplateRemove (data) {
      this.settings.MainlandSettings.SmsTemplate = {
        Id: null,
        Name: null
      }
    },
    onMainlandSmsSignatureRemove (data) {
      this.settings.MainlandSettings.SmsSignature = {
        Id: null,
        Name: null
      }
    },
    onInternationalSmsTemplateRemove (data) {
      this.settings.InternationalSettings.SmsTemplate = {
        Id: null,
        Name: null
      }
    },
    onInternationalSmsSignatureRemove (data) {
      this.settings.InternationalSettings.SmsSignature = {
        Id: null,
        Name: null
      }
    },
    async onClickSave () {
      try {
        this.isSaving = true
        await CharketSmsSettingsService.saveVerificationCodeSettings(this.settings)
        this.$bus.emit('success', { content: 'Verification Code Settings was saved.', type: 'success' })
        this.sourceSettings = JSON.parse(JSON.stringify(this.settings))
      } catch (error) {
        this.$bus.emit('error', { content: error.message, type: 'error' })
      } finally {
        this.isSaving = false
      }
    },
    async getSettings () {
      try {
        this.$bus.emit('show-loading')
        this.settings = await CharketSmsSettingsService.getVerificationCodeSettings()
        this.sourceSettings = JSON.parse(JSON.stringify(this.settings))
      } catch (error) {
        this.$bus.emit('error', { content: error.msg, type: 'error' })
      } finally {
        this.$bus.emit('hide-loading')
      }
    }
  }
}
</script>
<style lang="scss" scoped>
</style>
